.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.topBar{
  background-color: black;
  color: white;
  text-align: center;
  height: 3rem;
  font-size: 2rem;
  max-width: 100%;
}
img{
  width: 100%;
  
}
.content{
  
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerContent{
  width: 70%;
  box-sizing: border-box;
 
}
.title{
  text-align: center;
}
footer{
  background-color: black;
  color: white;
  text-align: center;
}